import React from 'react'

import Navbar from './Navbar.jsx'

function Diagnose() {
    var img = '';
    return(
        <React.Fragment>
            <Navbar />
            <div className="row">
                <div className="col-md-5">
                    <div style={{margin:"40px", border:" 3px solid #4085F0", borderRadius: "6px", padding: "10px", width: "100%"}}>
                        <label for="nome">Nome do Paciente</label>
                        <input type="text" style={{background:" #4085F0", color: "#fff"}} className="form-control" id="nome" ></input>
                        <br></br>
                        <label for="data">Data de Nascimento</label>
                        <input type="date" style={{background:" #4085F0", color: "#fff"}} className="form-control" id="data" ></input>
                        <br></br>
                        <label for="cpf">CPF</label>
                        <input type="text" style={{background:" #4085F0", color: "#fff"}} className="form-control" id="cpf" ></input>
                        
                    </div>
                    <div style={{marginLeft: "40px", width: "100%"}}>
                        <p style={{fontFamily: "Droid Sans", fontSize: "20px", fontWeight: "bold"}}>Radiografia:</p>
                        <label className="labelFile" for="select">Carregar Imagem:</label>
                        <input type="file" id="select" />
                    </div>
                </div>
                <div className="col-md-7">
                    <div style={{marginLeft:"40px", marginTop: "20px", marginRight: "20px",alignContent: "center", border: "3px solid gray", borderRadius: "6px", height: "80%", textAlign: "center", alignItems: "center"}}>
                        { img? <div style={{marginTop:"20px"}}> 
                                <img src={img} alt="image" style={{width:"50%"}}/>
                            </div>
                            : 
                            <p style={{margin: "100px", fontFamily: "Droid Sans", fontSize: "20px", fontWeight: "bold", color: "gray"}}>Sem Imagem carregada.</p> 
                        }
                        
                    </div>
                    <div style={{marginTop: "20px", marginLeft: "40px", marginRight: "20px",  height: "20%"}}>
                        <button style={{marginLeft: "30px", marginTop:"15px", marginRight: "100px", width: "50%", height: "60%", fontSize: "30px", fontWeight: "bold"}} className="btn btn-danger">Iniciar </button>
                        
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default Diagnose